import Vue from 'vue'
import Router from 'vue-router'
/*import HelloWorld from '@/components/HelloWorld'*/
import reservation from '@/page/index'
import logo from '@/page/logo'

Vue.use(Router)  

export default new Router({ 

  routes: [ 
    {
      path: '/reservation',
   
      name: 'reservation',
      component: reservation,
      meta:{
      	title:"预约"
      }
    },
     {
      path: '/logo',
      name: 'logo',
      component: logo,
      meta:{
      	title:"登录"
      }
    },
   
    
    
    //subscribeshow
    
   
  
  ]
})



